.dialogsPage {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 2fr 10fr;
}

.dialogs {
  border-right: 1px solid #eee;
  padding: 20px;
}

.messages {
  padding: 20px;
}

.messages__body {
  margin-bottom: 20px;
}

.messages__textarea {
  width: 100%;
  height: 47px;
}

.messages__textarea textarea {
  height: 100%;
  border: 1px solid#ddd;
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  outline: none;
  font-size: 20px;
  line-height: 47px;
  padding: 0 10px;
  overflow: hidden;
}

.messages__footer {
  display: flex;
  align-items: center;
}

.messages__add button{
  /* height: 70px; */
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}