.user {
  display: flex;
  align-items: center;
  background-color: bisque;
  padding: 20px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}

.user__avatar {
  width: 60px;
  height: 60px;
  border-radius: 5px;
  overflow: hidden;
}

.user__avatar img {
  object-fit: cover;
  width: 100%;
}

.user__name {
  margin-left: 5px;
}

.user__name.active {
  font-weight: 700;
}
